div.container.wordContainer {
    height: 100%;
    min-width: 180px;
    width: 250px;
}

@media screen and (orientation: portrait) and (max-width: 400px) {
    div.container.wordContainer {
        width: 100vw;
    }
}

@media screen and (orientation: portrait) and (min-width: 360px) and (max-width: 550px) {
    div.container.wordContainer {
        width: 50vw;
    }
}

@media screen and (min-width: 740px) {
    div.container.wordContainer {
        width: 25vw;
        max-width: 300px;
    }
}